<template>
  <div class="content">
    <myhead/>
    <dl class="navlist" v-for="(item,index) in this.navdata" :key="index">
      <dt>
        <router-link :to="{name:'wangbadetail',params:{index:index,name:item.name}}">
          <img :src="item.img" alt="">
        </router-link>
      </dt>
      <dd>
        <h3 class="name">{{item.name}}</h3>
        <p class="text">{{item.text}}</p>
        <span class="address">{{item.address}}</span>
      </dd>
    </dl>
  </div>
</template>

<script>
  import myhead from '@/components/myhead'
  import mock from '@/mock'
  import axios from 'axios'
  export default {
    data(){
      return {
        navdata:'',
        navid:''
      }
    },
    components:{
      myhead
    },
    mounted(){
      localStorage.navid = this.$route.params.navid;
      this.getJs()
    },
    methods:{
      getJs(){
        this.navid = localStorage.navid || this.$route.params.navid;
        axios.get('/api/navdetail').then(res => {
          for(let key in res.data) {
            if(key === this.$route.params.navid){
              this.navdata = res.data[key]
            }
          } 
        })
      }
    }
  }
</script>

<style scoped>
.content{
  width:100%;
  height:100%;
  overflow-y:scroll;
}
.navlist{
  width:100%;
  height:10rem;
  display:flex;
  justify-content: flex-start;
  align-items: center;
  margin-top:1rem;
}
.navlist dt a{
  width:10rem;
  height:10rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border: .1rem solid #ccc;
}
.navlist dt a img{
  width:80%;
  height:80%;
}
.navlist dd{
  flex:1;
  margin-left:1rem;
}
.name{
  font-size:1.8rem;
}
.text{
  color:#000;
  padding-top:1.5rem;
}
.address{
  float: right;
  color:rgb(18, 2, 160);
  padding-right:.5rem;
  padding-top:1.5rem;
}
</style>


